import firebase from 'firebase'

const config = {
    apiKey: "AIzaSyCrSAEPkZvzZ2COWNdFCeBnw2yKkr2nKmg",
    authDomain: "vb18-288ff.firebaseapp.com",
    databaseURL: "https://vb18-288ff.firebaseio.com",
    projectId: "vb18-288ff",
    storageBucket: "vb18-288ff.appspot.com",
    messagingSenderId: "958361084699"
};

export class FirebaseController {
    firebaseApp;
    db;
    data;
    teams;

    constructor() {
        this.firebaseApp = firebase.initializeApp(config);
        this.db = this.firebaseApp.database();
        this.db.ref('data').once("value",  (res) => {
            this.data = res.val();
            this.teams = this.data.teams;
        }, function (err) {
            console.log(err)
        })
    }


    getTeam(teamId){
        console.log(teamId)
        //TODO unsafe
        return this.teams[teamId - 1]
    }
    getMatches() {
        return this.data;
    }

    saveBet(matchId, bet) {
        const currentUserId = firebase.auth().currentUser.uid;
        return this.db.ref('bets/' + currentUserId + "/" + matchId).set(bet);
    }

    getOwnBets(){
        const currentUserId = firebase.auth().currentUser.uid;
        return this.db.ref('bets/' + currentUserId).once('value');
    }

    getPublicBets(){
        return this.db.ref('bets'   ).once('value');
    }

    getFirebaseApp() {
        return this.firebaseApp;
    }

    getCurrentUserId() {
        return firebase.auth().currentUser.uid;
    }

    isLoggedIn() {
        return firebase.auth().currentUser !== null;
    }

    saveUser() {
        const currentUser = firebase.auth().currentUser;
        return this.db.ref('users/' + currentUser.uid).set({username: currentUser.displayName})
    }
}

const fireabseController = new FirebaseController();
export default fireabseController;