import Vue from 'vue';
import Router from 'vue-router';
import Phase from './views/Phase.vue';
import Matches from './views/Matches.vue';
import Stadiums from './views/Stadiums.vue';
import Login from './views/Login.vue';
import MyBets from './views/MyBets.vue';
import PublicBets from './views/PublicBets.vue';
import firebaseController from './firebaseController';

Vue.use(Router);

const router = new Router({
    routes: [
        {
            path: '/',
            name: 'phase',
            component: Phase,
        },
        {
            path: '/matches',
            name: 'matches',
            component: Matches,
        },
        {
            path: '/stadiums',
            name: 'stadiums',
            component: Stadiums,
        },
        {
            path: '/login',
            name: 'login',
            component: Login
        },
        {
            path: '/my-bets',
            name: 'myBets',
            component: MyBets
        },
        {
            path: '/public-bets',
            name: 'publicBets',
            component: PublicBets
        }
    ],
});

router.beforeEach((to, from, next) => {
    console.log(to)
    if (firebaseController.isLoggedIn() || to.name == 'login'){
        next();
    }
    else{
        router.push('/login')
    }
});

export default router;
